<template>
  <a-modal 
    :maskClosable="false" 
    :title="$t('common.detail')" 
    :width="1000" 
    v-model="isShow"
    @ok="onSubmit"
    @cancel="cancel" 
    :destroyOnClose="true">
    <template slot="footer">
      <a-button key="back" @click="cancel" >{{ this.$t('common.btn_cancel') }}</a-button>
    </template>
    <div class="table-page-search-wrapper">
      <s-table  
        ref="table"
        :bordered="false"
        :autoLoad="true"
        :data="loadData"
        size="small"
        rowKey="stealId"
      >
        <a-table-column 
          :title="this.$t('task.index1')"
          width="50px"
          align="center"
        >
          <span slot-scope="text">
            <a-icon type="schedule" theme="twoTone" class="icon" style="font-size:25px"/>
          </span>
        </a-table-column>
        <a-table-column 
          :title="$t('meter.index1')"
          width="150px"
          dataIndex="meterDescription"
        >
        </a-table-column>
        <a-table-column
          :title="this.$t('steal.index2')"
          width="150px"
          dataIndex="themeId"
        >
        </a-table-column>
        <a-table-column
          :title="this.$t('steal.index1')"
          width="150px"
        >
          <span slot-scope="text,record">
            <span>{{ $t('steal.theme'+record.themeId) }}</span>
          </span>
        </a-table-column>
        <a-table-column
          :title="this.$t('steal.index3')"
          width="150px"
        >
          <span slot-scope="text,record">
            <span>{{ $t('steal.rule'+record.stealId) }}</span>
          </span>
        </a-table-column>
        <a-table-column
          :title="$t('common.status')"
          width="150px"
          dataIndex="status"
        >
          <span slot-scope="text,record">
            <span>{{ $t('common.process'+record.status) }} </span>
          </span>
        </a-table-column>
        <a-table-column
          :title="this.$t('steal.factor')"
          width="150px"
          dataIndex="suspicion"
        >
        </a-table-column>
      </s-table>
    </div>
  </a-modal>
</template>

<script>
  import STable from '@/components/table/'
  import {
    axios
  } from '@/utils/request'
  export default {
    components: {
      STable,
    },
    data() {
      return {
        isShow: false,
          loadData:  parameter => { 
          const that = this
          parameter.deviceId = that.params.deviceId
          parameter.nowTime = that.params.nowTime
          return axios({
            url: '/steal/analysis/search-detail',
            method: 'get',
            params: Object.assign('', parameter)
          }).then(res => {           
            return res.result
          })
        },
      }
    },
    methods: {
      showModal(params) {
        this.isShow = true
        this.params=params
      },
      onSubmit () {        
        this.isShow = false
      },
      cancel() {
        this.isShow = false
      },
    },
  }
</script>
