<template>
  <a-modal 
    :maskClosable="false" 
    :title="$t('common.detail')" 
    :width="1000" 
    v-model="isShow"
    @ok="onSubmit"
    @cancel="cancel" 
    :destroyOnClose="true">
    <div class="table-page-search-wrapper">
      <a-form layout="inline">
        <a-row :gutter="20">
          <a-col :md="6" :sm="24">
            <a-form-item :label="this.$t('device.index1')">
              <span>{{ params.deviceNum }}</span>
            </a-form-item>
          </a-col>
          <a-col :md="6" :sm="24">
            <a-form-item :label="this.$t('common.time')">
              <span>{{ params.nowTime }}</span>
            </a-form-item>
          </a-col>
          <a-col :md="6" :sm="24">
            <a-form-item :label="this.$t('common.status')">
              <a-select v-model="status" >
                <a-select-option value="0">
                  {{ $t('common.process0') }}
                </a-select-option>
                <a-select-option value="1">
                  {{ $t('common.process1') }}
                </a-select-option>
              </a-select>
            </a-form-item>
          </a-col>
          <a-col :md="4" :sm="48">
            <span class="table-page-search-submitButtons">
              <btn-search @click="onSearch" :showInput="false"></btn-search>
            </span>
          </a-col>
        </a-row>
      </a-form>
      <s-table  
        ref="table"
        :bordered="false"
        :autoLoad="true"
        :data="loadData"
        size="small"
        rowKey="stealId"
        :rowSelection="{type:'checkbox', fixed: false, onChange: onSelect}"
      >
        <a-table-column 
          :title="this.$t('task.index1')"
          width="50px"
          align="center"
        >
          <span slot-scope="text">
            <a-icon type="schedule" theme="twoTone" class="icon" style="font-size:25px"/>
          </span>
        </a-table-column>
        <a-table-column
          :title="this.$t('steal.index2')"
          width="100px"
          dataIndex="themeId"
        >
        </a-table-column>
        <a-table-column
          :title="this.$t('steal.index1')"
          width="150px"
        >
          <span slot-scope="text,record">
            <span>{{ $t('steal.theme'+record.themeId) }}</span>
          </span>
        </a-table-column>
        <a-table-column
          :title="this.$t('steal.index3')"
          width="150px"
        >
          <span slot-scope="text,record">
            <span>{{ $t('steal.rule'+record.stealId) }}</span>
          </span>
        </a-table-column>
        <a-table-column
          :title="$t('common.status')"
          width="120px"
          dataIndex="status"
        >
          <span slot-scope="text,record">
            <span>{{ $t('common.process'+record.status) }} </span>
          </span>
        </a-table-column>
        <a-table-column
          :title="this.$t('steal.ptype')"
          width="150px"
        >
          <span slot-scope="text,record">
            <a-select v-model="record.opType" >
              <a-select-option value="0">
                {{ $t('steal.ptype0') }}
              </a-select-option>
              <a-select-option value="1">
                {{ $t('steal.ptype1') }}
              </a-select-option>
            </a-select>
          </span>
        </a-table-column>
        <a-table-column
          :title="this.$t('steal.pcontent')"
          width="250px"
        >
          <span slot-scope="text,record">
            <a-input v-model="record.opContent" />
          </span>
        </a-table-column>
      </s-table>
    </div>
  </a-modal>
</template>

<script>
  import STable from '@/components/table/'
  import {
    axios
  } from '@/utils/request'
  export default {
    components: {
      STable,
    },
    data() {
      return {
        isShow: false,
        selectedRows: [],
        status:'0',
        params:{deviceNum:'',nowTime:''},
        result:true,
        loadData:  parameter => { 
          const that = this
          parameter.deviceId = that.params.deviceId
          parameter.status = that.status
          parameter.nowTime = that.params.nowTime
          return axios({
            url: '/steal/analysis/search-detail',
            method: 'get',
            params: Object.assign('', parameter)
          }).then(res => {           
            return res.result
          })
        },
      }
    },
    methods: {
      showModal(params) {
        this.isShow = true
        this.params=params
        this.status='0'
        this.result=true
        this.selectedRowKeys = this.selectedRows = []
      },
      onSubmit () {        
        if(this.selectedRows.length) {
          console.log(this.selectedRows)
          this.selectedRows.forEach(item => {
            axios({
              url: '/steal/analysis/update',
              method: 'post',
              params: {deviceId:item.deviceId,nowTime:item.nowTime,stealId:item.stealId,opType:item.opType,opContent:item.opContent,status:'1'}
            }).then(result=>{
              if(!result){
                this.result=false
              }
            })   
          })
          if(this.result){
            this.$emit('ok')
            this.isShow = false
          } 
        }else{
           this.$message.info(this.$t('common.selectone'))
        }
      
      },
      onSearch(){
        this.$refs.table.refresh()
      },
      cancel() {
        this.isShow = false
      },
      onSelect(selectedRowKeys, selectedRows) {
        this.selectedRows = selectedRows
      },
    },
  }
</script>
