<template>
  <div>
    <div>
      <a-tabs default-active-key="1" @change="callback" :activeKey="type" type="card" class="headtab">
        <a-tab-pane key="1" :tab="this.$t('steal.showtype1')">
        </a-tab-pane>
        <a-tab-pane key="2" :tab="this.$t('steal.showtype2')">
        </a-tab-pane>
      </a-tabs>
      <a-card style="background-color:#fff8db;margin-bottom:10px" v-show="show">
        <div class="table-page-search-wrapper">
          <a-form class="ant-advanced-search-form searchFixed" >
            <a-row :gutter="32">
              <a-col :xl="7" :md="11" :sm="48" style="padding-left:0;">
                <a-form-item :label="$t('dept.index')" >
                  <dept-select ref="dept" @change="(value)=>{value?queryParam.deptId=value:delete queryParam.deptId; this.queryData()}"/>
                </a-form-item>
              </a-col>
              <a-col :xl="7" :md="11" :sm="48" style="padding-left:0;">
                <a-form-item :label="this.$t('dataedit.device_num')">
                  <a-input-search v-model="queryParam.deviceNum" :placeholder="$t('dataedit.device_num_placeholder')" @search="SelectDev" />
                </a-form-item>
              </a-col>
              <a-col :xl="7" :md="11" :sm="48" style="padding-left:0;" >
                <a-form-item :label="this.$t('steal.factor')+'>'">
                  <a-input v-model="factor" />
                </a-form-item>
              </a-col>
            </a-row>
            <a-row :gutter="32" style="margin-top:10px">
              <a-col :xl="7" :md="11" :sm="48" style="padding-left:0;">
                <a-form-item :label="$t('common.time')" >
                  <pick-range :defaultValue="stratRange" showLevel="2" @change="onChangeTime" />
                </a-form-item>
              </a-col>
              <a-col :xl="7" :md="11" :sm="48" style="padding-left:0;">
                <a-form-item :label="$t('common.status')" >
                  <a-select v-model="queryParam.status" :allowClear="true">
                    <a-select-option value="0">{{ $t('common.process0') }}</a-select-option>
                    <a-select-option value="1">{{ $t('common.process1') }}</a-select-option>
                  </a-select>
                </a-form-item>
              </a-col>
              <a-col :xl="7" :md="11" :sm="48" style="padding-left:80px;">
                <a-form-item >
                  <span>
                    <btn-search @click="queryData" :showInput="false"></btn-search>&nbsp; &nbsp;
                    <btn-export @click="exportData" :showInput="false"></btn-export>
                  </span>
                </a-form-item>
              </a-col>
            </a-row>
          </a-form>
        </div>
      </a-card>
      <a-card>
        <div class="table-page-search-wrapper" style="margin-bottom: 10px;">
          <a-row>
            <a-col :md="19" :sm="19">
            </a-col>
            <a-col :md="5" :sm="5">
              <sorter @refresh="$refs.table.refresh()" @show="showSearch"></sorter>
            </a-col>
          </a-row>
        </div>
        <s-table 
          ref="table" 
          rowKey="deviceId,nowTime"
          :data="loadData" 
          :bordered="false" 
          v-if="type=='1'"
        > 
          <a-table-column 
            :title="$t('task.index1')"
            width="50px"
            align="center"
          >
            <span slot-scope="text">
              <a-icon type="schedule" theme="twoTone" class="icon" style="font-size:25px"/>
            </span>
          </a-table-column>
          <a-table-column
            :title="$t('common.status')"
            width="150px"
            dataIndex="status"
          >
            <span slot-scope="text,record">
              <span v-if="record.status.indexOf('0')>-1">{{ $t('common.process0') }} </span>
              <span v-if="record.status.indexOf('0')<0">{{ $t('common.process1') }} </span>
            </span>
          </a-table-column>
          <a-table-column
            :title="$t('common.time')"
            width="150px"
            dataIndex="nowTime"
          >
          </a-table-column>
          <a-table-column
            :title="$t('dataedit.device_num')"
            width="150px"
            dataIndex="deviceNum"
          >
          </a-table-column>
          <a-table-column 
            :title="$t('meter.index1')"
            width="150px"
            dataIndex="meterDescription"
          >
          </a-table-column>
          <a-table-column 
            :title="$t('device.index4')"
            width="150px"
            data-index="meterSort"
          >
            <span slot-scope="text,record">
              <span>{{ $t('dct.METER_SORT'+record.meterSort) }}</span>
            </span>
          </a-table-column>
          <a-table-column 
            :title="$t('steal.factor')"
            width="150px"
            dataIndex="sumSuspicion"
          >
          </a-table-column>
          <a-table-column 
            :title="$t('dept.index')"
            width="150px"
            dataIndex="deptId"
            :customRender="deptId => this.$refs.dept.getLabel(deptId)"
          >
          </a-table-column>
          <a-table-column 
            :title="$t('steal.event')"
            width="150px"
          >
            <span slot-scope="text,record">
              <a-button type="link" @click="showEvent(record)" >{{ record.countStealId }}</a-button>
            </span>
          </a-table-column>
          <a-table-column 
            :title="$t('steal.chart')"
            width="150px"
          >
            <span slot-scope="text,record">
              <a-icon type="line-chart" @click="showChart(record)" :style="{ fontSize: '16px' ,color:'#1890FF'}"/>
            </span>
          </a-table-column>
          <a-table-column
            :title="$t('task.action')"
            dataIndex="action"
            width="120px"
          > 
            <span slot-scope="text,record">
              <a-button type="link" @click="onUpdate(record)" v-if="record.status.indexOf('0')>=0" >{{ $t('common.process') }}</a-button>
            </span>
          </a-table-column>
        </s-table>
        <s-table 
          ref="table" 
          rowKey="deviceId,nowTime,stealId"
          :data="loadData" 
          :bordered="false"
          v-if="type=='2'" 
        > 
          <a-table-column 
            :title="$t('task.index1')"
            width="50px"
            align="center"
          >
            <span slot-scope="text">
              <a-icon type="schedule" theme="twoTone" class="icon" style="font-size:25px"/>
            </span>
          </a-table-column>
          <a-table-column
            :title="$t('common.status')"
            width="150px"
            dataIndex="status"
          >
            <span slot-scope="text,record">
              <span>{{ $t('common.process'+record.status) }} </span>
            </span>
          </a-table-column>
          <a-table-column
            :title="$t('common.time')"
            width="150px"
            dataIndex="nowTime"
          >
          </a-table-column>
          <a-table-column
            :title="$t('dataedit.device_num')"
            width="150px"
            dataIndex="deviceNum"
          >
          </a-table-column>
          <a-table-column 
            :title="$t('meter.index1')"
            width="150px"
            dataIndex="meterDescription"
          >
          </a-table-column>
          <a-table-column 
            :title="$t('device.index4')"
            width="150px"
            data-index="meterSort"
          >
            <span slot-scope="text,record">
              <span>{{ $t('dct.METER_SORT'+record.meterSort) }}</span>
            </span>
          </a-table-column>
          <a-table-column
            :title="$t('steal.index1')"
            width="150px"
            dataIndex="themeId"
          >
            <span slot-scope="text,record">
              <span>{{ $t('steal.theme'+record.themeId) }}</span>
            </span>
          </a-table-column>
          <a-table-column 
            :title="$t('steal.index3')"
            width="150px"
            dataIndex="id"
          >
            <span slot-scope="text,record">
              <span>{{ $t('steal.rule'+record.stealId) }}</span>
            </span>
          </a-table-column>
          <a-table-column 
            :title="$t('steal.factor')"
            width="150px"
            dataIndex="suspicion"
          >
          </a-table-column>
          <a-table-column 
            :title="$t('dept.index')"
            width="150px"
            dataIndex="deptId"
            :customRender="deptId => this.$refs.dept.getLabel(deptId)"
          >
          </a-table-column>
          <a-table-column 
            :title="$t('steal.chart')"
            width="150px"
          >
            <span slot-scope="text,record">
              <a-icon type="line-chart" @click="showChart(record)" :style="{ fontSize: '16px' ,color:'#1890FF'}"/>
            </span>
          </a-table-column>
          <a-table-column
            :title="$t('task.action')"
            dataIndex="action"
          > 
            <span slot-scope="text,record">
              <a-button type="link" @click="onUpdate(record)" v-if="record.status!='1'" >{{ $t('common.process') }}</a-button>
            </span>
          </a-table-column>
        </s-table>
      </a-card>
      <show-details ref="showdetails" @ok="queryData" ></show-details>
      <show-chart ref="showchart" @ok="queryData" ></show-chart>
      <process ref="process" @ok="queryData" ></process>
      <search-meter ref="selectdev" @ok="loadDevData" :params="{}" :multiple="false"></search-meter>
    </div>
  </div>
</template>
<script>
  import STable from '@/components/table/'
  import { axios } from '@/utils/request'
  import Dict from '@/views/public/Dict'
  import { toUTC } from '@/inhe/picker/utils'
  import DeptSelect from '@/views/public/DeptSelect'
  import Process from './Process'
  import ShowChart from '@/views/steal/analysis/ShowChart'
  import ShowDetails from './ShowDetails'
  import SearchMeter from '@/views/public/SearchMeter'

  export default {
    name: 'Index',
    components: {
      STable,
      Dict,
      DeptSelect,
      Process,
      ShowChart,
      ShowDetails,
      SearchMeter,
    },
    data() {
      return {
        show:true,
        queryParam: {deviceNum:'',status:''},
        type:'1',
        factor:'',
        startTime:toUTC(new Date(new Date().getFullYear()+'-01')),
        endTime: toUTC(new Date()),
        stratRange:[toUTC(new Date(new Date().getFullYear()+'-01')),toUTC(new Date())],
        loadData:  parameter => { 
          const that = this
          parameter.suspicion=that.factor
          parameter.type = that.type
          parameter.startTime=that.startTime
          parameter.endTime=that.endTime
          return axios({
            url: '/steal/analysis/search',
            method: 'get',
            params: Object.assign(parameter, this.queryParam)
          }).then(res => {           
            return res.result
          })
        },

      }
    },
    methods: {
      showChart(record){
        this.$refs.showchart.showModal(record)
      },
      showEvent(record){
        this.$refs.showdetails.showModal(record)
      },
      onUpdate(record){
        this.$refs.process.showModal(record)
      },
      SelectDev(){
        this.$refs.selectdev.showModal()
      },
      loadDevData(item){
       this.queryParam.deviceNum=item[0].deviceNum
      },
      onChangeTime(value){
        value instanceof Array?this.startTime=value[0]:this.startTime=''
        value instanceof Array?this.endTime=value[1]:this.endTime=''
      },
      callback(key){
        this.type=key
        this.$refs.table.refresh()
      },
      showSearch() {
        this.show = !this.show
      },
      exportData() {        
        const that = this
        const data = {
          isoCode: localStorage.getItem('locale')
        }  
        data.startTime = that.startTime
        data.endTime = that.endTime
        data.type=this.type
        data.status instanceof Array?that.queryParam.status = that.queryParam.status[0]:''      
        axios({
          url: '/steal/analysis/export',
          method: 'get',
          params: Object.assign(data, this.queryParam),
          responseType: 'blob'
        }).then(res => {
          const url = window.URL.createObjectURL(new Blob([res]))
          const link = document.createElement('a')
          link.style.display = 'none'
          link.href = url
          link.setAttribute('download', this.$t('function._29')+'.xls')
          document.body.appendChild(link)
          link.click()          
        })
      },
      //输入任务名称的search,上方列表表刷新
      queryData(){
        this.$refs.table.refresh()
      }
    }
  }
</script>
<style lang="scss" scoped>
.headtab{
  background: #fff;
}
.ant-tabs>.ant-tabs-bar{
  margin: 0 !important;
}
.ant-form-item-children .ant-calendar-picker{
  width: 100%;
}
</style>