<template>
  <a-modal 
    :maskClosable="false" 
    :title="$t('common.detail')" 
    :width="1000" 
    v-model="isShow"
    @ok="onSubmit"
    @cancel="cancel" 
    :destroyOnClose="true">
    <template slot="footer">
      <a-button key="back" @click="cancel" >{{ this.$t('common.btn_cancel') }}</a-button>
    </template>
    <div class="table-page-search-wrapper">
      <a-form layout="inline">
        <a-row :gutter="20">
          <a-col :md="9" :sm="24">
            <a-form-item :label="this.$t('vee.datatype')">
              <a-select v-model="type">
                <a-select-option value="0">
                  {{ $t('kwh.type0') }}
                </a-select-option>
                <a-select-option value="1">
                  {{ $t('kwh.type1') }}
                </a-select-option>
              </a-select>
            </a-form-item>
          </a-col>
          <a-col :md="9" :sm="24" v-show="type=='0'">
            <a-form-item :label="this.$t('common.time')">
              <pick-range :defaultValue="stratRange" showLevel="1" @change="onChangeTime" />
            </a-form-item>
          </a-col>
          <a-col :md="9" :sm="24" v-show="type=='1'">
            <a-form-item :label="this.$t('common.time')">
              <pick-range :defaultValue="stratRange1" showLevel="2" @change="onChangeTime" />
            </a-form-item>
          </a-col>
          <a-col :md="3" :sm="48">
            <span class="table-page-search-submitButtons">
              <btn-search @click="onSearch" :showInput="false"></btn-search>
            </span>
          </a-col>
          <a-col :md="1" :sm="48">
            <a-button type="link" @click="showTable" style="font-size:16px">
              <a-icon type="table" v-if="showType"/>
              <a-icon type="line-chart" v-if="!showType"/>
            </a-button>
            
          </a-col>
        </a-row>
      </a-form>
      <s-table  
        ref="table"
        :bordered="false"
        :autoLoad="true"
        :data="loadData"
        size="small"
        rowKey="deviceId,nowTime"
        v-show="!showType"
      >
        <a-table-column 
          :title="this.$t('task.index1')"
          width="50px"
          align="center"
        >
          <span slot-scope="text">
            <a-icon type="schedule" theme="twoTone" class="icon" style="font-size:25px"/>
          </span>
        </a-table-column>
        <a-table-column
          :title="this.$t('common.date')"
          width="150px"
          dataIndex="nowTime"
        >
        </a-table-column>
        <a-table-column
          :title="this.$t('kwh.kwh')"
          width="150px"
          dataIndex="totalKwh"
        >
        </a-table-column>
        <a-table-column
          :title="this.$t('kwh.data1')"
          width="150px"
          dataIndex="kwhT1"
        >
        </a-table-column>
        <a-table-column
          :title="this.$t('kwh.data2')"
          width="150px"
          dataIndex="kwhT2"
        >
        </a-table-column>
        <a-table-column
          :title="this.$t('kwh.data3')"
          width="150px"
          dataIndex="kwhT3"
        >
        </a-table-column>
        <a-table-column
          :title="this.$t('kwh.data4')"
          width="150px"
          dataIndex="kwhT4"
        >
        </a-table-column>
      </s-table>
      <v-chart :force-fit="true" :height="height" :data="data" :scale="scale" v-if="showType">
        <v-tooltip />
        <v-axis />
        <v-legend />
        <v-line position="nowTime*kWh" color="type" />
        <v-point position="nowTime*kWh" color="type" :size="4" :v-style="style" :shape="'circle'" />
      </v-chart>
    </div>
  </a-modal>
</template>

<script>
  import STable from '@/components/table/'
  import { toUTC } from '@/inhe/picker/utils'
  import {
    axios
  } from '@/utils/request'
  const scale = [{
    dataKey: 'nowTime',
  //  alias: this.$t('kwh.kwh')
  }]
  const DataSet = require('@antv/data-set')
  export default {
    components: {
      STable,
    },
    data() {
      return {
        isShow: false,
        type:'0',
        showType:true,
        scale,
        data:[],
        height: 400,
        style: { stroke: '#fff', lineWidth: 1 },
        startTime:toUTC(new Date(new Date().getFullYear()+'-'+(new Date().getMonth()+1))),
        endTime: toUTC(new Date()),
        stratRange:[toUTC(new Date(new Date().getFullYear()+'-'+(new Date().getMonth()+1))),toUTC(new Date())],
        startTime1:toUTC(new Date(new Date().getFullYear()+'-01')),
        endTime1: toUTC(new Date()),
        stratRange1:[toUTC(new Date(new Date().getFullYear()+'-01')),toUTC(new Date())],
        loadData:  parameter => { 
          const that = this
          parameter.deviceId = that.params.deviceId
          parameter.startTime = that.type=='0'?that.startTime:that.startTime1
          parameter.endTime = that.type=='0'?that.endTime:that.endTime1
          parameter.timeType = that.type
          return axios({
            url: '/steal/analysis/search-kwh',
            method: 'get',
            params: Object.assign('', parameter)
          }).then(res => {   
            that.getLineData(res.result.data)        
            return res.result
          })
        },
      }
    },
    methods: {
      showModal(params) {
        this.isShow = true
        this.params=params
        this.startTime=toUTC(new Date(new Date().getFullYear()+'-'+(new Date().getMonth()+1)))
        this.endTime=toUTC(new Date())
        this.startTime1=toUTC(new Date(new Date().getFullYear()+'-01'))
        this.endTime1=toUTC(new Date())
      },
      onSubmit () {        
        this.isShow = false
      },
      onSearch(){
        this.$refs.table.refresh()
      },
      showTable(){
        this.showType=!this.showType
      },
      onChangeTime(value){
        if(this.type=='0'){
          value instanceof Array?this.startTime=value[0]:this.startTime=''
          value instanceof Array?this.endTime=value[1]:this.endTime=''
        }else{
          value instanceof Array?this.startTime1=value[0]:this.startTime1=''
          value instanceof Array?this.endTime1=value[1]:this.endTime1=''
        }
       
      },
      cancel() {
        this.isShow = false
      },
      getLineData(data) {
        const that = this
        that.data=[]
        const sourceData=[]
        for (let i = data.length-1; i >=0; i--) {
          sourceData.push({
           nowTime:data[i].nowTime,
           [this.$t('kwh.data1')]:data[i].kwhT1,
           [this.$t('kwh.data2')]:data[i].kwhT2,
           [this.$t('kwh.data3')]:data[i].kwhT3,
           [this.$t('kwh.data4')]:data[i].kwhT4,
           [this.$t('kwh.kwh')]:data[i].totalKwh,
          })
          const dv = new DataSet.View().source(sourceData)
          dv.transform({
            type: 'fold',
            fields: [this.$t('kwh.kwh'),this.$t('kwh.data1'), this.$t('kwh.data2'),this.$t('kwh.data3'), this.$t('kwh.data4')],
            key: 'type',
            value: 'kWh',
          })
          that.data = dv.rows
        }
      }
    },
  }
</script>
